




















import axios from 'axios'
import {Component} from 'vue-property-decorator'
import {Pageable} from '@/components/Pageable'
import Pagination from '@/components/Pagination.vue'
import Modal from '@/components/Modal.vue'
import {goTop} from '@/utils/utils'
import configService from '@/services/config.service'
import {Role} from '@/models/Account'
import {Tag} from '@/models/Note'

@Component<Pageable>({
  components: {
    Modal,
    Pagination
  },
  watch: {
    '$route'(to) {
      this.page = +to.query.page || 1
      this.load()
    }
  }
})
export default class TagList extends Pageable {
  modal: boolean = false
  tags: Tag[] = []

  get admin(): boolean {
    return this.$store.state.user.role == Role[Role.ROLE_ADMIN]
  }

  mounted() {
    configService.setTitle('标签')
    this.size = 100
    this.page = +this.$route.query.page || 1
    this.load()
  }

  load() {
    axios.get(`/tags?${this.query}`).then(({data}) => {
      this.tags = data.content
      this.totalPages = data.totalPages
      this.totalElements = data.totalElements
      goTop()
    })
  }

  go(page: number) {
    this.$router.push(this.$route.path + '?page=' + page)
  }
}
