






















import {Component, Prop, Vue} from 'vue-property-decorator'

@Component
export class Super extends Vue {
  show: boolean = false
}

@Component<Super>({
  model: {
    prop: 'open',
    event: 'changed'
  },
  watch: {
    open(newVal) {
      this.show = newVal
    }
  }
})
export default class Modal extends Super {
  @Prop() private open!: boolean
  @Prop({default: true}) private closable!: boolean
  @Prop() private title!: string
  @Prop() private size!: string
  @Prop({default: '确认'}) private okText!: string
  @Prop({default: '取消'}) private cancelText!: string

  get sizeClass() {
    return {
      [this.size]: true
    }
  }

  close() {
    this.show = false
    this.$emit('changed', false)
  }

  onDimmer(event: Event) {
    if (this.closable && this.show && event.target === event.currentTarget) {
      this.close()
    }
  }

  onSubmit() {
    this.$emit('submit')
    this.close()
  }

  onCancel() {
    this.$emit('cancel')
    this.close()
  }
}
