




















































import axios from 'axios'
import {Component} from 'vue-property-decorator'
import {Pageable} from '@/components/Pageable'
import Pagination from '@/components/Pagination.vue'
import Modal from '@/components/Modal.vue'
import {Category} from '@/models/Category'
import {goTop} from '@/utils/utils'
import configService from '@/services/config.service'
import {Role} from '@/models/Account'

@Component<Pageable>({
  components: {
    Modal,
    Pagination
  },
  watch: {
    '$route'(to) {
      this.page = +to.query.page || 1
      this.load()
    }
  }
})
export default class CategoryList extends Pageable {
  modal: boolean = false
  loading: boolean = false
  category: Category = new Category()
  categories: Category[] = []

  get admin(): boolean {
    return this.$store.state.user.role == Role[Role.ROLE_ADMIN]
  }

  mounted() {
    configService.setTitle('分类')
    this.page = +this.$route.query.page || 1
    this.load()
  }

  load() {
    this.loading = true
    axios.get(`/categories?${this.query}`).then(({data}) => {
      this.categories = data.content
      this.totalPages = data.totalPages
      this.totalElements = data.totalElements
      this.loading = false
      goTop()
    }, () => {
      this.loading = false
    })
  }

  go(page: number) {
    this.$router.push(this.$route.path + '?page=' + page)
  }

  submit() {
    axios.post(`/categories`, this.category).then(({data}) => {
      this.category = new Category()
      this.categories.splice(0, 0, data)
      this.modal = false
    })
  }
}
